.history-section {
    border: 1px solid darkgray;
    width: 25%;
    padding: 10px;
    border-radius: 4px;
    margin-left: 50px;
    background-color: rgb(228, 226, 226);
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

@media (max-width: 768px) {
    .history-section {
        width: 40%;
    }
}

@media (max-width: 576px) {
    .history-section {
        padding: 5px;
        margin-top: 10px;
        margin-left: 0;
        width: 97%;
    }
}

